import React from "react";
import { CardColumns, Spinner } from "react-bootstrap";

const CodeCardContainer = () => {
  const InstallCard = React.lazy(() => import("./cards/InstallCard"));
  const RunningTestsCard = React.lazy(() => import("./cards/RunningTestsCard"));
  const SimpleTestCard = React.lazy(() => import("./cards/SimpleTestCard"));
  const BrowserSupportCard = React.lazy(() =>
    import("./cards/BrowserSupportCard")
  );
  const FixtureCard = React.lazy(() => import("./cards/FixtureCard"));
  const ClickActionCard = React.lazy(() => import("./cards/ClickActionCard"));
  const DragActionCard = React.lazy(() => import("./cards/DragActionCard"));

  return (
    <CardColumns>
      <React.Suspense
        fallback={<Spinner animation="grow" variant="secondary" />}
      >
        <InstallCard />
      </React.Suspense>
      <React.Suspense
        fallback={<Spinner animation="grow" variant="secondary" />}
      >
        <RunningTestsCard />
      </React.Suspense>
      <React.Suspense
        fallback={<Spinner animation="grow" variant="secondary" />}
      >
        <SimpleTestCard />
      </React.Suspense>
      <React.Suspense
        fallback={<Spinner animation="grow" variant="secondary" />}
      >
        <BrowserSupportCard />
      </React.Suspense>
      <React.Suspense
        fallback={<Spinner animation="grow" variant="secondary" />}
      >
        <FixtureCard />
      </React.Suspense>
      <React.Suspense
        fallback={<Spinner animation="grow" variant="secondary" />}
      >
        <ClickActionCard />
      </React.Suspense>
      <React.Suspense
        fallback={<Spinner animation="grow" variant="secondary" />}
      >
        <DragActionCard />
      </React.Suspense>
    </CardColumns>
  );
};

export default CodeCardContainer;
